/* General Styles */
.main-container {
  width: 100vw;
  background-color: #333;
  display: flex;
  text-align: center;
  justify-content: space-between;
}

/* Container */
.container {
  width: 90vw;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  gap: 20px;
  max-width: 1200px;
  margin: 0 auto;
}

/* Content */
.content {
  flex: 1 1 300px;
  max-width: 600px;
}

.about-heading {
  font-size: 2.5em;
  margin-bottom: 20px;
  transition: color 0.5s ease; /* Smooth transition for color change */
}

.about-description {
  font-size: 1.2em;
  color: #ddd;
  line-height: 1.6;
}

/* Image Container */
.image-container {
  flex: 1 1 300px;
  text-align: center;
}

.image {
  width: 100%;
  max-width: 300px;
  height: 250px;
  border-radius: 50%;
}

.name {
  margin-top: 15px;
  font-size: 1.5em;
  color: #007bff;
}

/* Media Queries */
@media (max-width: 768px) {
  .about-heading {
      font-size: 2em;
  }

  .about-description {
      font-size: 1em;
  }

  .name {
      font-size: 1.3em;
  }
}

@media (max-width: 480px) {
  .about-heading {
      font-size: 1.8em;
  }

  .about-description {
      font-size: 0.9em;
  }

  .name {
      font-size: 1.2em;
  }
}

@media screen and (max-width : 550px) {
  .main-container {
    flex-direction: column;
  }
}