/* Item Container */
.item-container {
  min-width: 15vw;
  min-height: 100vh;
  padding-top: 40px;
  padding: 10px;
  color: #ddd;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  background-color: #333; /* Add a background color for better visibility */
  padding-left: 0;
}

/* Items List */
.items {
  list-style-type: none;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  height: auto;
  padding-left: 0;
  padding: 10px;
  flex-wrap: wrap;
  width: 100%;
  max-width: 200px;
}

/* Individual Item */
.item {
  width: 100%;
  max-width: 130px;
  font-size: 20px;
  font-weight: bold;
  color: #ddd;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  text-decoration: none;
  transition: background-color 0.3s, color 0.3s;
}

/* Active Tab */
.item.active {
  background-color: #007bff; /* Background color for active tab */
  color: #fff; /* Text color for active tab */
  border-radius: 2px;
}

/* Hover Effect */


/* Media Queries */
@media screen and (min-width: 550px) and (max-width: 760px) {
  .item-container {
      min-width: 30vw;
      height: 100vh;
      flex-direction: row;
      justify-content: flex-start;
  }
  .items {
      flex-direction: row;
      height: auto;
      justify-content: center;
      align-items: flex-start;
  }
}

@media screen and (max-width: 549px) {
  .item-container {
      min-width: 100vw;
      min-height: 100px;
      border-top-right-radius: 0;
      border-bottom-left-radius: 10px;
      flex-direction: column;
      justify-content: flex-start;
      align-items: center;
      padding: 10px;
  }
  .items {
      max-height: 50px;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: center;
      align-items: center;
      margin: 0;
      padding: 0;
  }
  .item {
      width: auto;
      font-size: 16px;
      padding: 5px;
  }
}

@media screen and (max-width: 280px) {
  .item {
      font-size: 12px;
      padding: 5px;
  }
}
